import * as THREE from "three";
import { Transform } from "./Transform";
/**
 * scene is a container for all game objects
 */
export class Scene extends THREE.Scene {
    registerTransform(transform) {
        this.add(transform);
        const gameObject = transform.gameObject;
        if (gameObject.activeInHierarchy) {
            transform.traverseVisible(item => {
                if (item instanceof Transform)
                    item.gameObject.foreachComponent(c => {
                        if (c.enabled)
                            c.onEnable();
                    }); //tryEnableComponents
            });
            transform.traverseVisible(item => {
                if (item instanceof Transform)
                    item.gameObject.foreachComponent(c => {
                        if (c.enabled)
                            c.unsafeTryCallStart();
                    }); //tryStartComponents
            });
        }
    }
    /**
     * add gameObject to scene
     * @param gameObjectBuilder
     */
    addChildFromBuilder(gameObjectBuilder) {
        const gameObject = gameObjectBuilder.build();
        gameObjectBuilder.initialize();
        if (gameObject.unsafeGetTransform() instanceof Transform) {
            this.registerTransform(gameObject.unsafeGetTransform()); //it"s safe because it use same logic as GameObject.registerTransform()
        }
        else {
            throw new Error("unreachable");
        }
    }
}
